<template>
  <div class="base-info">
      <div class="desc border-bottom">
          <div class="left">
              <p class="score"><em>{{this.score}}</em>分 很棒</p>
              <p class="comment-tips"><span>{{this.commentNum}}条评论</span><span>{{this.tipsNum}}条攻略</span></p>
              <span class="iconfont right-icon">&#xe69b;</span>
          </div>
          <div class="right">
              <p>景点简介</p>
              <p>开放时间、贴士</p>
              <span class="iconfont right-icon">&#xe69b;</span>
          </div>
      </div>
      <div class="adress border-top">{{this.adress}}<span class="iconfont right-icon">&#xe69b;</span></div>
  </div>
</template>

<script>
export default {
  name: 'BaseInfo',
  props: {
    score: Number,
    commentNum: Number,
    tipsNum: Number,
    adress: String
  }
}
</script>

<style lang="stylus" scoped>
.base-info
  position relative
  top -.1rem
  padding .1rem .2rem 0 .2rem
  margin-bottom .1rem
  background #fff
  border-radius .1rem .1rem 0 0
  .desc
    display flex
    width 100%
    padding-top .1rem
    padding-bottom .2rem
    .left
      position relative
      flex 1
      .score
        height 0.5rem
        color #ff8300
        line-height .44rem
        em
          font-size .44rem
      .comment-tips
        height 0.32rem
        span
          font-size .24rem
          color #9e9e9e
          line-height .32rem
          margin-right .2rem
      .right-icon
        position absolute
        top .3rem
        right .2rem
        color #9e9e9e
        font-size .24rem
    .right
      position relative
      flex 1
      p:nth-of-type(2n+1)
        font-size .28rem
        color #212121
        line-height .48rem
        padding-left .3rem
      p:nth-of-type(2n)
        color #9e9e9e
        font-size .24rem
        padding-left .3rem
      .right-icon
        position absolute
        top .3rem
        right .2rem
        color #9e9e9e
        font-size .24rem
  .adress
    display block
    position relative
    padding .2rem 0
    line-height .36rem
    color #212121
    .right-icon
      position absolute
      top .3rem
      right .2rem
      color #9e9e9e
      font-size .24rem
</style>



