<template>
  <div class="detail-page">
    <fixed-header :title="this.title" textColor="light" bgColor="dark" v-show="showHeader" class="detail-header"></fixed-header>
    <div class="detail-main">
      <v-banner :title="this.title" :bannerImg="this.bannerImg" :star="this.star" :sightImgsNum="sightImgsNum" :id="this.$route.params.id"></v-banner>
      <base-info :score="score" :commentNum="commentNum" :tipsNum="tipsNum" :adress="adress"></base-info>
      <tickets-list :lianpiao="lianpiao"></tickets-list>
    </div>
    <v-footer class="detail-footer"></v-footer>
    <!-- 返回 icon -->
    <div class="iconfont back-icon" @click="back" v-show="!showHeader">
      <span class="icon-bg"></span>
      <span class="icon-text">&#xe624;</span>
    </div>
  </div>
</template>

<script>
import FixedHeader from '@/components/fixed-header/FixedHeader.vue'

import VBanner from '@/components/banner/Banner.vue'
import BaseInfo from '@/components/base-info/BaseInfo.vue'
import TicketsList from '@/components/tickets-list/TicketsList.vue'

import VFooter from '@/components/footer/Footer.vue'

import axios from 'axios'

export default {
  name: 'Detail',
  components: {
    FixedHeader,
    VBanner,
    BaseInfo,
    TicketsList,
    VFooter
  },
  data () {
    return {
      showHeader: false,
      title: '',
      bannerImg: '',
      star: '',
      sightImgsNum: NaN,
      score: 8.0,
      commentNum: 123,
      tipsNum: 45,
      adress: '',
      lianpiao: [],
      oldId: []
    }
  },
  methods: {
    getDetailInfo () {
      axios.get('/api/detail.json', {
        params: {
          id: this.$route.params.id
        }
      }).then(this.handleGetDataSucc)
    },
    handleGetDataSucc (res) {
      res = res.data
      const data = res.data
      this.title = data.title
      this.bannerImg = data.bannerImg
      this.star = data.star
      this.sightImgsNum = data.sightImgsNum
      this.score = data.score
      this.commentNum = data.commentNum
      this.tipsNum = data.tipsNum
      this.adress = data.adress
      this.lianpiao = data.lianpiao
    },
    back () {
      this.$router.back(-1)
    }
  },
  mounted () {
    this.getDetailInfo()
    this.oldId.push(this.$route.params.id)
  },
  activated () {
    const nowId = this.$route.params.id
    if (this.oldId.indexOf(nowId) === -1) {
      this.getDetailInfo()
      this.oldId.push(nowId)
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~assets/styles/varibles.styl'

.detail-page
  position absolute
  top 0
  left 0
  width 100%
  min-height 100%
  background-color $home-page-bgColor

.detail-main
  padding-top $fixed-header-height+0.2rem
  overflow hidden

.back-icon
  position fixed
  left 0.1rem
  top 0.1rem
  width 0.72rem
  height 0.72rem
  .icon-bg
    display block
    width 0.72rem
    height 0.72rem
    background-color #000000
    opacity 0.5
    border-radius 50%
  .icon-text
    display block
    position absolute
    left 0
    top 0
    width 0.72rem
    height 0.72rem
    line-height 0.72rem
    text-align center
    color #ffffff
    font-size 0.36rem
    font-weight 600
</style>



