<template>
  <div class="banner">
    <router-link tag="div" :to="'/sightImgs/' + id" class="img-wrapper">
      <img :src="bannerImg" :alt="currentTitle">
    </router-link>
    <div class="img-info">
      <div class="title">{{currentTitle}}</div>
      <div class="sum">
        <span class="iconfont img-icon">&#xe622;</span>
        {{this.sightImgsNum}}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'VBanner',
  props: {
    title: String,
    star: String,
    bannerImg: String,
    sightImgsNum: Number,
    id: String
  },
  computed: {
    currentTitle () {
      return `${this.title}(${this.star}景区)`
    }
  }
}
</script>

<style lang="stylus" scoped>
  .banner
    position relative
  .img-wrapper
    overflow hidden
    width 100%
    height 0
    padding-bottom 55%
    img
      width 100%
  .img-info
    position absolute
    width 100%
    height 0.48rem
    line-height 0.48rem
    display flex
    bottom 0.1rem
    background-color #ffffff30
    color #ffffff
    .title
      flex 1
      font-size 0.36rem
      margin-left 0.4rem
    .sum
      min-width 1.2rem
</style>



