<template>
  <div class="fixed-header border-bottom"
    :class="cls"
  >
    <!-- 返回图标 -->
    <div class="left iconfont back-icon" @click="back">&#xe624;</div>
    <!-- title 标题 -->
    <div class="title">{{title}}</div>
    <!-- right -->
    <div class="right"></div>
  </div>
</template>

<script>
export default {
  name: 'FixedHeader',
  props: {
    title: {
      type: String,
      default: 'FixedHeader---fixed on top'
    },
    textColor: {
      type: String,
      default: 'dark'
    },
    bgColor: {
      type: String,
      default: 'light'
    }
  },
  computed: {
    cls () {
      return `textColor-${this.textColor} bg-${this.bgColor}`
    }
  },
  methods: {
    back () {
      this.$router.back(-1)
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~assets/styles/varibles.styl'
@import '~assets/styles/mixins.styl'

.fixed-header
  position fixed
  width 100%
  height $fh-height
  display flex
  z-index 1
  &.bg-light
    background-color $fh-bgColor-light
  &.bg-dark
    background-color $fh-bgColor-dark
  &.textCls-light
    color $fh-textColor-light
  &.textCls-dark
    color $fh-textColor-dark

.left
  width $fh-height
  height $fh-height
  center()
  font-size 0.48rem

.title
  flex 1
  font-size 0.32rem
  ellipsis()
  center()

.right
  width $fh-height
  height $fh-height
  center()
</style>



