<template>
  <div class="tickets-list">
    <div class="ticket-group border-topbottom" v-for="(item, index) in this.lianpiao" :key="index">
      <div class="group-title" @click="taggle">
        <div class="title">{{item.title}}</div>
        <div class="price">
          <span class="price-icon">￥</span>
          <span class="price-num">{{item.price}}</span>
          起
        </div>
        <span class="iconfont right-icon">&#xe7ff;</span>
      </div>
      <ul class="group-list">
        <li class="ticket border-topbottom" :class="{ 'ticket-show' : showTicket }" v-for="(ticket, index) in item.tickets" :key="index">
          <div class="ticket-main">
            <div class="name">{{ticket.name}}</div>
            <div class="desc">{{ticket.desc}}</div>
            <div class="supplier">{{ticket.supplier}}</div>
            <div class="label">
              <span v-for="(item, index) in ticket.label" :key="index">{{item}}</span>
            </div>
          </div>
          <div class="ticket-side">
            <div class="side-wrapper">
              <div class="price">￥<em class="price-num">{{ticket.price}}</em></div>
              <div class="order">预定</div>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TicketsList',
  props: {
    lianpiao: [Array]
  },
  data () {
    return {
      showTicket: true
    }
  },
  methods: {
    taggle () {
      this.showTicket = !this.showTicket
    }
  }
}
</script>

<style lang="stylus" scoped>
.tickets-list
  position relative
  margin-bottom 0.1rem
  background-color #ffffff
  min-height 3rem
  .ticket-group
    .group-title
      position relative
      padding 0.24rem 0.2rem
      background #fff
      .title
        margin-right 1.8rem
        color #333
        font-size 0.3rem
        line-height 0.48rem
      .price
        right 0.46rem
        font-size 0.24rem
        overflow hidden
        position absolute
        top 0.3rem
        height 0.4rem
        line-height 0.4rem
        color #9e9e9e
        .price-icon
          color #ff9800
        .price-num
          color #ff9800
          font-size 0.4rem
      .right-icon
        position absolute
        top 0.5rem
        right 0.2rem
        color #bbbbbb
        font-size 0.24rem

.group-list
  .ticket.ticket-show
    display flex

.group-list
  .ticket
    background-color #f5f5f5
    padding 0.2rem
    box-sizing border-box
    display none
    .ticket-main
      flex 3
      .name
        padding 0
        color #616161
        font-size 0.28rem
        line-height 0.4rem
      .desc
        display inline-block
        height 0.32rem
        color #616161
        font-size 0.24rem
        line-height 0.32rem
        white-space nowrap
      .supplier
        margin-top 0.08rem
        color #616161
        font-size 0.24rem
        line-height 0.34rem
      .label
        font-size 0
        span
          display inline-block
          margin-top 0.08rem
          padding 0 0.04rem
          height 0.32rem
          color #00afc7
          font-size 0.2rem
          line-height 0.32rem
          white-space nowrap
    .ticket-side
      flex 1
      display flex
      align-items center
      flex-direction column
      justify-content center
      .side-wrapper
        width 100%
        padding-left 0.1rem
        box-sizing border-box
        display block
        .price
          display block
          overflow hidden
          font-size .24rem
          text-align center
          height 0.4rem
          color #ff9800
          .price-num
            font-size 0.4rem
            line-height 0.4rem
        .order
          color #fff
          height 0.6rem
          text-align center
          font-size 0.28rem
          line-height 0.6rem
          border-radius 0.08rem
          background-image linear-gradient(130deg, #ffab1e 37%, #ff8c12 100%)
</style>



